import React, { Component } from 'react';
import '../css/Menu.css'

class Menu extends Component {
  constructor(props) {
    super(props);
    this.state = {
      displayed_form: '',
      logged_in: localStorage.getItem('token') ? true : false,
      username: ''
    };
  }
  componentDidMount() {
    if (this.state.logged_in) {
      fetch('https://api-cookie.herokuapp.com/api/current_user/', {
        headers: {
          Authorization: `JWT ${localStorage.getItem('token')}`
        }
      })
        .then(res => res.json())
        .then(json => {
          this.setState({ username: json.username });
        });
    }
  }
  render() {
    let loggedIn = this.state.logged_in
    return(
    <div>
      <button className="menu" onClick={onClickButton} aria-label="Main Menu">
        <svg width="50" height="50" viewBox="0 0 100 100">
          <path className="line line1" d="M 20,29.000046 H 80.000231 C 80.000231,29.000046 94.498839,28.817352 94.532987,66.711331 94.543142,77.980673 90.966081,81.670246 85.259173,81.668997 79.552261,81.667751 75.000211,74.999942 75.000211,74.999942 L 25.000021,25.000058" />
          <path className="line line2" d="M 20,50 H 80" />
          <path className="line line3" d="M 20,70.999954 H 80.000231 C 80.000231,70.999954 94.498839,71.182648 94.532987,33.288669 94.543142,22.019327 90.966081,18.329754 85.259173,18.331003 79.552261,18.332249 75.000211,25.000058 75.000211,25.000058 L 25.000021,74.999942" />
        </svg>
      </button>
        <div id="mySidebar" className="sidebar">
              <a href={loggedIn ? '/dashboard' : '/'}>{loggedIn ? 'Dashboard' : 'Home'}</a>
              <a href="/about">About</a>
              <a href="/tutorial">Tutorial</a>
              <a href={loggedIn ? '/profile/' + this.state.username : '/login'}>{loggedIn ? 'Profile' : 'Login'}</a>
              <a href="/report">Report</a>
              <a href="/contact">Contact</a>
        </div>
    </div>
    )
  }
}

function onClickButton() {
  let menuButton = document.querySelector('.menu')
  menuButton.classList.toggle('opened');
  menuButton.setAttribute('aria-expanded', menuButton.classList.contains('opened'));
  let sideBar = document.getElementById("mySidebar")
  console.log(sideBar.style.width)
    if (sideBar.style.width === "250px") {
      sideBar.style.width = "0px";
    } else {
      sideBar.style.width = "250px";
    }
}

export default Menu